import React, { useState } from 'react';
import { useDispatch } from 'react-redux';
import PlacesAutocomplete, { geocodeByAddress, getLatLng } from 'react-places-autocomplete';
import { useTheme } from 'emotion-theming';
import { addOrigin, addDestination, removeTrip } from '../store/trips';

const GoogleAutoComplete = ({ type }) => {
  const [address, setAddress] = useState("")
  const [id, setId] = useState('')
  const theme = useTheme();
  const dispatch = useDispatch();
  
  const searchOptions = {
    types: ['address']
  }

  const handleSelect = async value => {
    const results = await geocodeByAddress(value);
    const latLng = await getLatLng(results[0]);

    setAddress(value)

    dispatch(
      type === 'origin' ? addOrigin({
        id: results[0].place_id,
        name: results[0].formatted_address, 
        coords: { 
          lat: latLng.lat, 
          lng: latLng.lng 
        }
      }) : addDestination({
        id: results[0].place_id,
        name: results[0].formatted_address, 
        coords: { 
          lat: latLng.lat,
          lng: latLng.lng
        } 
      })
    )
    setId(results[0].place_id)    
  };

  const clearField = event => {
    event.preventDefault()

    dispatch(
      removeTrip({ tripType: type, id })
    )
    setAddress('')
  }
    
  return (
    <>
    <PlacesAutocomplete 
      value={address}
      onChange={setAddress}
      onSelect={handleSelect}
      searchOptions={searchOptions}
    >
      {({ getInputProps, suggestions, getSuggestionItemProps, loading }) => (
      <div>
        <input
          name="address"
          type="text"
          id="main-inputs"
          {...getInputProps({ 
            placeholder: type === 'origin' ? "Pickup Address": "Dropoff Address"
          })}
        /> 
        <div>
          { suggestions.map(suggestion => {
            const style = {
              fontFamily: 'Open Sans',
              color: !suggestion.active ? `${theme.colors.green}` : `white`,
              backgroundColor: suggestion.active ? `${theme.colors.violet}` : `${theme.colors.blue}`,
              padding: `.25rem`,
              width: `75%`,
              borderTop: suggestion.active ? "1px solid white" : null,
              borderBottom: suggestion.active ? "1px solid white" : null,
              cursor: 'pointer'
            }
            return (
              <div {...getSuggestionItemProps(suggestion, { style })}> 
                { suggestion.description }
              </div>
            )
          })}
        </div>
      </div>
      )} 
    </PlacesAutocomplete>
      <button onClick={clearField}>x</button>
    </>
  )
}

export default GoogleAutoComplete;