import React from 'react'
import GoogleMapReact from 'google-map-react'
import { useSelector } from 'react-redux'

const MapMarker = ({ type }) => <div id="map-marker">{ type === 'origin' ? 'O' : 'D'}</div>

const GoogleMap = props => {
  const origins = useSelector(state => state.entities.trips.origin)
  const destinations = useSelector(state => state.entities.trips.destination)

  return (
    <div style={{ height: '100%', width: '100%' }}>
    <GoogleMapReact
      bootstrapURLKeys={{ key: 'AIzaSyC0VaGsv4vdS6aBw7otqrikEI4ykWbQRbE' }}
      defaultCenter={{ lat: 40.310134, lng: -74.27660709999999 }}
      defaultZoom={8}
      yesIWantToUseGoogleMapApiInternals
      onGoogleApiLoaded={({map, maps}) => { 
        console.log(maps)
        }
      }
    >
      { 
        origins.map(origin => (
          <MapMarker
            type={'origin'}
            key={origin.id} 
            lat={origin.lat} 
            lng={origin.lng} 
          />
        ))
      }
      {
        destinations.map(dest => (
          <MapMarker
            type={'destination'}
            key={dest.id}
            lat={dest.lat}
            lng={dest.lng}
          />
        ))
      }
    </GoogleMapReact>
    </div>
  )
}

export default GoogleMap;