import React from 'react'

const UserAvatar = ({ src }) => {
  return (
    <>
    <div style={{ display: 'block' }}>
    { src ? 
      <img
        style={{ 
          width: '30px',
          border: 0,
          borderRadius: '50%',
          padding: 0,
          margin: 0,
        }}
        src={src}
      /> : '...loading' }
      </div>
    </>
  )
}

export default UserAvatar;