import { createSlice } from '@reduxjs/toolkit';

const trips = {
  origin: [],
  destination: [],
}

const slice = createSlice({
  name: 'trips',
  initialState: trips,
  reducers: {
    addOrigin: (trips, action) => {
      const { id, name, coords } = action.payload;
      if(trips.origin.length < 1) {
        trips.origin.push({ id, name, lat: coords.lat, lng: coords.lng })
      } else {
        return console.error('cannot add more than one origin point')
      }
    },
    addDestination: (trips, action) => {
      const { id, name, coords } = action.payload;
      if(trips.destination.length < 1) {
        trips.destination.push({ id, name, lat: coords.lat, lng: coords.lng })
      } else {
        return console.error('cannot add more than one destination point')
      }
    },
    tripAssignedToUser: (trips, action) => {
      const { tripId, userId } = action.payload;
      const index = trips.origin.findIndex(trip => trip.id === tripId);
      trips[index].userId = userId;
    },
    removeTrip: (trips, action) => {
      const { tripType, id } = action.payload;
      if(trips[tripType].length != 0) {
        const index = trips[tripType].findIndex(trip => trip.id === id)
        trips[tripType].pop(index)
      } else {
        return console.error('no trips to remove!')
      }
    },
    directionsResponseReceived: (trips, action) => {
      const { response } = action.payload;
      const resObj = Object.assign({}, response);
      trips.response = resObj;
    }
  }
});

export const { 
  addOrigin, 
  addDestination, 
  tripAssignedToUser, 
  removeTrip,
  directionsResponseReceived
} = slice.actions;
export default slice.reducer;