/** @jsx jsx */
import React, { useEffect, useState } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { css, jsx } from '@emotion/core';
import GoogleMap from '../Components/GoogleMap';
import Button from '../Components/Button';
import { useTheme } from 'emotion-theming';
import GoogleAutoComplete from '../Components/GoogleAutoComplete';
import { userAdded, removeUser } from '../store/users';

const Dashboard = () => {
  const dispatch = useDispatch();
  const state = useSelector(state => state);
  const theme = useTheme();
  
  useEffect(() => {
    if('geolocation' in navigator) {
      navigator.geolocation.getCurrentPosition((success, error) => {
        if(error) console.log(error)
        let lat = success.coords.latitude
        let lng = success.coords.longitude
      })
    } else {
      console.warn('this browser does not support geolocation - user location unavailable')
    }
  }, []);

  const addUsers = () => {
    dispatch(
      userAdded({ 
        id: 1, 
        firstname: 'brent', 
        lastname: 'abruzese', 
        email: 'brent.abruzese@gmail.com',
        avatar: 'https://www.pictureofsomething.com/jpg',
      }
    ))
  }

  const removeUsers = () => {
    dispatch(
      removeUser({ id: 1 })
    )
  }
  
  return (
  <>
    <div css={css`
      display: grid;
      grid-template-columns: 1fr 1fr;
      width: 100%;
      height: 50%;
      grid-gap: 1rem;
    `}>
      <GoogleMap />
        <form>
          <h1 style={{ color: theme.colors.violet }}>It's Party Time.</h1>
            <p>Let's get started! First, let us know a central pickup location for your group!</p>
          <div style={{ margin: '.9rem 0rem'}}>
          </div>
          <div style={{ margin: `1rem 0rem`}}>
            <GoogleAutoComplete type="origin" />
            <GoogleAutoComplete type="destination" />
            <Button>
              submit
            </Button>
            <a className="button is-primary">Primary</a>
          </div>
        </form>
    </div>
    <button onClick={addUsers}>add user</button>
    <button onClick={removeUsers}>delete user</button>
  </>
  )
}

export default Dashboard;