/** @jsx jsx */
import React, {useState} from 'react';
import { css, jsx, keyframes } from '@emotion/core'
import { BrowserRouter as Router,
  Link,
  Switch,
  Route,
} from 'react-router-dom'
import { useTheme } from 'emotion-theming'
import UserAvatar from '../Components/UserAvatar'
import Button from './Button'
import facepaint from 'facepaint'

const Navbar = () => {
  const [loggedIn, setIsLoggedIn] = useState(false)
  const theme = useTheme();

  const breakpoints = [576, 768, 992, 1200]

  const mq = facepaint(
    breakpoints.map(bp => `@media (min-width: ${bp}px)`)
  )

  const mqClass = css(mq({
    display: ['block', 'block', 'flex', 'flex']
  }))

  return (
  <nav>
    <Router>
    <ul
      css={{ 
        display: 'flex',
        color: theme.colors.secondary,
        margin: '0rem',
        listStyle: 'none',
        alignItems: 'center',
        backgroundColor: theme.colors.secondary,
        fontFamily: theme.fonts.primary.fontFamily,
        padding: '1rem',
        justifyContent: 'space-between',
        '& .nav-link, li': {
          transition: 'all 1s ease',
          marginLeft: '.75rem',
          marginRight: '.75rem',
          color: theme.colors.lightgrey,
          textDecoration: 'none',
          '&:hover': { 
            color: theme.colors.violet,
          },
        }
      }}
    >
      <div 
        style={{ 
          color: theme.colors.lightgrey,
          flex: '1 0 0',
        }}
      >
        <span style={{ fontSize: `2rem`}}>Welcome to <strong>Book-A-Ride!</strong></span>
      </div>
      <li>
        <Link className="nav-link" to="/">Home</Link>
      </li>
      <li>
        <Link className="nav-link" to="/">Trips</Link>
      </li>
      <li>
        <Link className="nav-link" to="/">Payments</Link>
      </li>
      <li>
        <Link className="nav-link" to="/">Settings</Link>
      </li>
      { !loggedIn ? <Button action={() => setIsLoggedIn(true)}>
          <span>Login</span>
        </Button> : <UserAvatar style={{ flexGrow: 1 }} src={'https://i.pravatar.cc/150?img=8'} /> }
    </ul>
    </Router>
  </nav>
  )
}

export default Navbar;